import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  useMediaQuery,
  useTheme,
} from '@material-ui/core';
import { css } from 'emotion';
import AutoCompleteField from 'components/form/AutoCompleteField';
import DateField from 'components/form/DateField';
import MoneyField from 'components/form/MoneyField';
import NumberField from 'components/form/NumberField';
import PercentField from 'components/form/PercentField';
import UploadField from 'components/form/UploadField';
import React from 'react';
import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { bp } from 'theme';
import InsuranceProvider, { InsuranceProviderLabels } from 'types/InsuranceProvider';
import TextField from 'components/form/TextField';

const ContentStyle = css`
  ${bp.xs} {
    width: 100%
  }
  ${bp.xlMob} {
    width: 600px;
  }
`

interface NewInsurancePolicyForm {
  policyNumber: string;
  insuranceProvider?: InsuranceProvider;
  originalPolicy?: (File | string)[];
  proceedsAssignmentLetter?: (File | string)[];
  otherPolicyDocuments?: (File | string)[];
  maximumLiability?: number;
  discretionaryCreditLimit?: number;
  maxInvoicingDays?: number;
  maxExtensionDays?: number;
  insuredPerc?: number;
  policyStartDate: Date | null;
  policyEndDate: Date | null;
}

const defaultValues: NewInsurancePolicyForm = {
  policyNumber: '',
  insuranceProvider: undefined,
  policyStartDate: null,
  policyEndDate: null,
}


export interface NewInsurancePolicyDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (insurancePolicy: NewInsurancePolicyForm) => void;
}

const insuranceProviderOptions = [...InsuranceProviderLabels.entries()].map(([provider, label]) => 
  ({ label: label, value: provider })
);


function NewInsurancePolicyDialog({ isOpen, onClose, onSubmit }: NewInsurancePolicyDialogProps) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xlMob'))

  const { control, getValues } = useForm<NewInsurancePolicyForm>({
    defaultValues,
    mode: 'onChange',
  });

  useEffect(
    () => {
      // Unmount CallBack
      if (!isOpen) {
        reset();
      }
      return () => reset();
    },
    [isOpen]
  )

  function onModalClose() {
    onClose();
  }

  function reset() {
  }

  function onModalOK() {
    onSubmit(getValues());
  }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        Add Insurance Policy
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              name="policyNumber"
              control={control}
              label="Policy Number"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <AutoCompleteField
              name="insuranceProvider"
              control={control}
              label="Provider"
              options={insuranceProviderOptions}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <UploadField
              name="originalPolicy"
              label="Policy Document"
              control={control}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <UploadField
              name="proceedsAssignmentLetter"
              label="Proceeds Assignment Letter"
              control={control}
            />
          </Grid>
          <Grid item xs={12}>
            <UploadField
              name="otherPolicyDocuments"
              label="Other Policy Related Documents"
              control={control}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <DateField
              control={control}
              name="policyStartDate"
              label="Policy Start Date"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <DateField
              control={control}
              name="policyEndDate"
              label="Policy End Date"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <MoneyField
              name="maximumLiability"
              control={control}
              label="Maximum Policy Liability"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <MoneyField
              name="discretionaryCreditLimit"
              control={control}
              label="Discretionary Credit Limit"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <NumberField
              name="maxInvoicingDays"
              control={control}
              label="Max Invoicing Days"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <NumberField
              name="maxExtensionDays"
              control={control}
              label="Max Extension Days"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <PercentField
              name="insuredPerc"
              control={control}
              label="Insured Percentage"
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" onClick={onModalOK}>
          Add
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default NewInsurancePolicyDialog;
