import { Control, Controller, FieldError } from 'react-hook-form';
import { FieldName } from 'react-hook-form/dist/types/form';
import { FieldRules } from 'types/FieldRules';
import { css } from 'emotion';
import React from 'react';
import { KeyboardDatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { FormControl, InputLabel } from '@material-ui/core';

const LabelStyle = css`
  font-size: 11px;
  color: #777;
  line-height: 2em;
`;

const DatePickerStyles = css`
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

const DialogStyles = css`
  .MuiToolbar-root {
    padding-left: 24px;
    padding-right: 24px;
  }
`;

interface Props<T> {
  name: keyof T;
  control: Control<T>;
  label?: string;
  className?: string;
  error?: FieldError;
  rules?: FieldRules;
  placeholder?: string;
  multiline?: boolean;
  minRows?: number;
  // type?: 'text' | 'number' | 'password';
  disabled?: boolean;
  autoFocus?: boolean;
  transform?: (value: string) => any;
}

const DateField = <T extends object>(props: Props<T>) => {
  const {
    name,
    control,
    label,
    // error,
    rules,
    // type,
    placeholder = 'dd/mm/yyyy',
    disabled,
    autoFocus,
    // transform
  } = props;

  return (
    <FormControl
      fullWidth
    >
      <InputLabel className={LabelStyle} shrink>{label}</InputLabel>
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Controller
          control={control}
          name={name as FieldName<T>}
          rules={rules}
          render={({ onChange, onBlur, value }) => (
            <KeyboardDatePicker
              // variant="inline"
              autoOk
              DialogProps={{ className: DialogStyles }}
              className={DatePickerStyles}
              disabled={disabled}
              onBlur={onBlur}
              autoFocus={autoFocus}
              // clearable // TODO: add clear adornment if possible
              placeholder={placeholder}
              showTodayButton
              value={value}
              // onChange={handleDateChange}
              onChange={onChange}
              // onChange={date => handleDateChange(date)}
              // minDate={new Date()}
              format="dd/MM/yyyy"
            />
          )}
        />
      </MuiPickersUtilsProvider>
    </FormControl>
  );
};

export default DateField;
